.controls {
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}

.controls__button {
	font-size: 1.8rem;
	padding: 0.5rem;
	margin: 1rem 1rem 0 1rem;
	background: 0;
	border: 0;
}

.controls__button--recording {
	color: #860016;
}

.controls__bpm {
	font-size: 1.5rem;
	color: #fff;
}

.hidden {
	display: none;
}


