.step-row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
}

.step-row__title {
	margin-right: 0.2em;
	width: 60px;
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

.step-row__title--playing {
	background: #0055ff;
	-webkit-transition: none;
	-o-transition: none;
	transition: none;
}

.step-row__steps {
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	display: -ms-flexbox;
	display: flex;
}

.step-row__button {
	font-size: 1.8rem;
	padding: 0;
	margin: 0 1rem;
	background: 0;
	border: 0;
}


