.step-indicator-row {
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	display: -ms-flexbox;
	display: flex;
}

.step-indicator {
	background: #fff;
	border: 0;
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	margin: 0.3rem;
	min-width: 32px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-size: 1.8em;
	color:#DC143C;
}

.step-indicator--active {
  background: #004;
}

.hide {
	visibility: hidden;
}


